@import url('https://fonts.googleapis.com/css?family=Product%20Sans:100,200,300,400,600,700,800,900');
/* @import url('https://fonts.googleapis.com/css?family=Montserrat:400,600,700,800,900'); */
/* @import url('https://fonts.googleapis.com/css?family=Roboto:400,600,700,800,900'); */

.app-container{
  margin-top: -65px;
  transform: scale(0.8);
}
.inline-div{
  display: inline-block;
}
.nav-link {
  color: white;
  font-size: 20px;
  padding-left: 25px;
}
.logo-text{
  margin-left: 20px;
  font-size: 50px;
  font-weight: 400;
  margin-right: 20px;
}
.bars-img{
  max-width: 80px;
  float: right;
  vertical-align: middle;
}
.logo-img{
  vertical-align: middle;
  max-width: 330px;
  margin-bottom: 22px;
}
main{
background-color: red;
}
.tile-app-container{
  position: relative;
}
body {
  margin: 0px;
  font-family: 'Product Sans',Arial,Helvetica,sans-serif;
  /* font-family: 'Montserrat', sans-serif; */
  /* font-family: 'Roboto', sans-serif; */
  color: #fff;
  background: #333;
  overflow: hidden;
}

button{
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 4;
  width: 100px;
  height: 50px;
  border-radius: 5px
}

.logo{
  text-shadow: 0 0.025em 0 rgba(0,0,0,0.1);
  text-decoration: none;
  color: #fff;
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 22px;
  z-index: 5;
  letter-spacing: 0.5px;
  font-weight: 300;
}
.logoBold{
  font-weight: 400;
}
.squareContainer{
  position: absolute;
  top: 5px;
  left: 5px;
  z-index: 5;
}

.gridChooser{
  display: grid;
  grid: repeat(5,1fr) / repeat(5,1fr);
  width: 100%;
  height: 100%;
  
}
.square {
  margin: 1px;
  background: #fff;
  opacity: 0.7;
  cursor: pointer;
}
.square:hover{
  opacity: 1;
}
.white{
  opacity: 1;
}